<template>
  <div class="profile">
    <v-avatar :size="avatarSize">
      <img :src="require('@/assets/profile.jpg')" alt="Profile Picture" class="profile-pic" />
    </v-avatar>
    <h1 class="name">Kang Hyo In</h1>
    <p class="description">From concept to execution, turning ideas into robust digital solutions.</p>
    <div class="info">
      <p><strong>Gender:</strong> 남자</p>
      <p><strong>Birthday:</strong> 1993년 7월 26일 ({{ age }}세)</p>
      <p><strong>Email:</strong> <a href="mailto:dsn0726@gmail.com" class="email-link">dsn0726@gmail.com</a></p>
      <p><strong>Phone:</strong> <a href="tel:01036201261" class="phone-link">010-3620-1261</a></p>
      <p><strong>Location:</strong> 대한민국, 서울특별시 중랑구 망우1동</p>
      <p><strong>Education:</strong> 강원대학교, 컴퓨터공학과 (2012.03~2018.02)</p>
      <p><strong>Military service:</strong> 육군 포병, 병장 만기제대 (2013.08~2015.05)</p>
    </div>
  </div>
</template>

<script>
import { ref, computed, onMounted, onUnmounted } from 'vue';

export default {
  setup() {
    const avatarSize = ref(window.innerWidth < 768 ? 120 : 180);
    
    const updateSize = () => {
      avatarSize.value = window.innerWidth < 768 ? 120 : 180;
    };

    const birthYear = 1993;
    const currentYear = new Date().getFullYear();
    const age = computed(() => currentYear - birthYear);

    onMounted(() => {
      window.addEventListener('resize', updateSize);
    });

    onUnmounted(() => {
      window.removeEventListener('resize', updateSize);
    });

    return {
      avatarSize,
      age
    };
  },
};
</script>

