<template>
  <v-app>
    <!-- 상단바 -->
    <v-app-bar app color="#1E1E1E" elevation="4" class="custom-app-bar">
      <v-app-bar-nav-icon @click="drawer = !drawer" class="menu-icon"></v-app-bar-nav-icon>
      <v-toolbar-title class="title-gradient">Code. Debug. Repeat.</v-toolbar-title>
      
      <!-- IP 정보 추가 -->
      <v-spacer></v-spacer>
      <v-toolbar-title class="ip-info" v-if="ipInfo">{{ ipInfo }}</v-toolbar-title>
    </v-app-bar>

    <!-- 사이드바 메뉴 -->
    <v-navigation-drawer v-model="drawer" app class="custom-sidebar" :temporary="isMobile">
      <v-list>
        <v-list-item v-for="(item, index) in menuItems" :key="index" :class="{ 'active-item': activeSection === item.path }" @click="scrollToSection(item.path)">
          <v-list-item-icon>
            <v-icon class="menu-icon">{{ item.icon }}</v-icon>
          </v-list-item-icon>
          <v-list-item-content>
            <v-list-item-title class="menu-text">{{ item.title }}</v-list-item-title>
          </v-list-item-content>
        </v-list-item>
      </v-list>
    </v-navigation-drawer>

    <!-- 메인 컨텐츠 -->
    <v-main>
      <v-container class="content" ref="contentContainer">
        <div class="scrollable-content" @scroll="handleScroll">
          <section v-for="(item, index) in menuItems" :id="item.path.substring(1)" :key="index" class="section">
            <router-view></router-view>
          </section>
        </div>
      </v-container>
    </v-main>
  </v-app>
</template>

<script>
import { useRouter, useRoute } from "vue-router";
import { ref, onMounted, onBeforeUnmount, watch } from "vue";
import axios from 'axios';

export default {
  setup() {
    const router = useRouter();
    const route = useRoute();
    const drawer = ref(false);
    const isMobile = ref(window.innerWidth < 768);
    const activeSection = ref(route.path);
    const ipInfo = ref(""); // IP 정보 저장 변수

    const menuItems = [
      { path: "/about", title: "About Me", icon: "mdi-account" },
      { path: "/intro", title: "Intro", icon: "mdi-file-document" },
      { path: "/projects", title: "Projects", icon: "mdi-folder" },
      { path: "/skills", title: "Skills", icon: "mdi-star" },
    ];

    // 클라이언트의 IP 주소를 가져오는 함수
    const fetchClientIp = async () => {
      try {
        const response = await axios.get('https://api.ipify.org?format=json');
        const clientIp = response.data.ip; // 사용자 IP 가져오기
        fetchIpInfo(clientIp); // 해당 IP로 Whois 정보 가져오기
      } catch (error) {
        console.error("IP 조회에 실패했습니다:", error);
        ipInfo.value = "IP 정보를 불러오는 데 실패했습니다. (ipify)";
      }
    };

    // IP 정보 조회 함수
    const fetchIpInfo = async (ip) => {
      try {
        const response = await axios.get(`https://thingproxy.freeboard.io/fetch/http://whois.kisa.or.kr/openapi/whois.jsp`, {
          params: {
            query: ip,
            key: '2021110100525570940672',
            answer: 'json',
          },
        });
        const countryCode = response.data.whois.countryCode;
        const orgName =
          response.data.whois.english?.user?.netinfo?.orgName;

        if (orgName) {
          ipInfo.value = `IP: ${response.data.whois.query} (${countryCode}) (${orgName})`;  // IP 정보 표시
        } else {
          ipInfo.value = `IP: ${response.data.whois.query} (${countryCode}) (Organization name not available)`; // 조직명 없음 표시
        }
      } catch (error) {
        console.error("IP 조회에 실패했습니다:", error);
        ipInfo.value = "IP 정보를 불러오는 데 실패했습니다. (whois)";
      }
    };

    const scrollToSection = async (path) => {
      await router.push(path); // 페이지로 이동만 하고 스크롤은 하지 않음

      if (isMobile.value) {
        drawer.value = false;
      }
    };

    const handleScroll = () => {
      const sections = document.querySelectorAll(".section");
      sections.forEach((section) => {
        const rect = section.getBoundingClientRect();
        if (rect.top >= 0 && rect.top < window.innerHeight / 2) {
          activeSection.value = `/${section.id}`;
          router.replace(activeSection.value);
        }
      });
    };

    const checkMobile = () => {
      isMobile.value = window.innerWidth < 768;
    };

    onMounted(() => {
      window.addEventListener("resize", checkMobile);
      fetchClientIp(); // 컴포넌트가 마운트될 때 클라이언트 IP 조회
    });

    onBeforeUnmount(() => {
      window.removeEventListener("resize", checkMobile);
    });

    watch(route, (newRoute) => {
      activeSection.value = newRoute.path;
    });

    return {
      drawer,
      isMobile,
      menuItems,
      activeSection,
      scrollToSection,
      handleScroll,
      ipInfo, // IP 정보를 템플릿에서 사용
    };
  },
};
</script>

