.ip-info {
  font-size: 0.9rem; /* 글자 크기 축소 */
  font-weight: bold;
  color: #fff; /* 텍스트 색상 흰색으로 설정 */
  padding: 4px 10px; /* 패딩을 줄여서 공간 축소 */
  border-radius: 4px; /* 둥근 모서리 */
  display: inline-block;
  text-transform: uppercase;
  background-color: rgba(0, 0, 0, 0.6); /* 반투명 검정 배경 추가 */
  box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.2); /* 작은 그림자 추가 */
  transition: transform 0.3s ease;
}

