.custom-sidebar {
  background: linear-gradient(135deg, #1e1e1e, #2a2a2a);
  color: white;
  width: 250px;
}

.menu-text {
  color: white;
  font-weight: bold;
  transition: color 0.3s ease-in-out;
  font-size: 1.1rem;
}

.active-item {
  background: #007bff !important;
