                  "장비사: 일본 (sysmex) / 이탈리아 (alifax) / 프랑스 (stago / sebia / bioMerieux) / 독일 (siemens) / 미국 (bio-rad / beckman coulter / abbott / BD / cepheid / bruker / thermo fisher scientific / dynex) / 스위스 (roche) / 스페인 (werfen) / 인도네시아 (sansure biotech) / 대한민국 (LG생활건강)",
                  "분야: 혈액가스 / 혈액학 / 생화학 / 미생물 / 면역학 / 요화학 / 혈액은행 / 분자유전 / 세포유전"
              ],
              logo: "1.jpg"
            }
          ]
        }
      ]
    }
  },
  computed: {
    techStack() {
      return [...new Set(this.projects.flatMap(company => company.projects.flatMap(project => project.tech)))];
    },
    filteredProjects() {
      return this.projects.map(company => ({
        name: company.name,
        projects: company.projects.filter(project =>
          project.title.includes(this.search) &&
          (this.selectedTech === '' || project.tech.includes(this.selectedTech))
        ),
      })).filter(company => company.projects.length > 0);
    },
  },
  watch: {
    filteredProjects() {
      this.$nextTick(() => {
        window.scrollTo({ top: 0, behavior: 'smooth' });
      });
    }
  }
};
</script>

